.profile__name {
    margin: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 1.14;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 294px;
}
@media screen and (max-width: 424px) {
    .profile__name  {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 220px;
    }
}