.profile__description {
    margin: 0;
    margin-top: 0px;
    margin-top: 16px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 336px;
}
@media screen and (max-width: 424px) {
    .profile__description  {
        font-size: 14px;
        line-height: 1.2;
        margin-top: 14px;
        margin: 14px auto 0 auto;
        max-width: 200px;
    }
}